import Vue from "vue";
import VueRouter from "vue-router";
import PcPage from "@/views/PcPage.vue";
import MobilePage from "@/views/MobilePage.vue";
import Disease from "@/views/Disease.vue";
import Report from "@/views/Report.vue";
import TestFireBase from "@/views/TestFireBase.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/test",
    name: "TestFireBase",
    component: TestFireBase,
  },
  {
    path: "/pc",
    name: "PcPage",
    component: PcPage,
  },
  {
    path: "/report",
    name: "Report",
    component: Report,
  },
  {
    path: "/mobile/disease",
    name: "disease",
    component: Disease,
  },
  {
    path: "/mobile",
    name: "Mobile",
    component: MobilePage,
  },
  {
    path: "/",
    redirect: "/pc", // PC
  },
];

const router = new VueRouter({
  mode: "hash", //  hash 
  base: process.env.BASE_URL || "",
  routes,
});

//  beforeEach 
// router.beforeEach((to, from, next) => {
//   const userAgent = navigator.userAgent;

//   // 
//   const isMobile = /Mobile|Android|iPhone/.test(userAgent);

//   // PC /mobile/disease 
//   if (isMobile && !to.path.startsWith("/mobile") && to.path !== "/report") {
//     next("/mobile");
//   } 
  
//   // PCPC
//   else if (!isMobile && to.path !== "/pc" && to.path !== "/report") {
//     next("/pc");
//   } 
//   // 
//   else {
//     next();
//   }
// });

router.beforeEach((to, from, next) => {
  const userAgent = navigator.userAgent;

  // 
  const isMobile = /Mobile|Android|iPhone/.test(userAgent);

  // 
  if (isMobile && !to.path.startsWith("/mobile") && to.path !== "/report") {
    if (to.path !== "/mobile") {
      next("/mobile");  //  "/mobile" 
    } else {
      next();  // 
    }
  } else if (!isMobile && to.path.startsWith("/mobile")) {
    if (to.path !== "/pc") {
      next("/pc");  //  "/pc" 
    } else {
      next();  // 
    }
  } else {
    next();  // 
  }
});
export default router;
